.sidebar {
  width: 250px;
  height: 100vh;
  background-color: #f7f7f7; // Neutral background
  position: sticky;
  top: 0;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  box-shadow: 2px 4px 10px 1px rgba(0, 0, 0, 0.1);
  transition: width 0.3s ease; // Smooth transition for width
  overflow: hidden;

  &.collapsed {
    width: 60px; // Sidebar is collapsed
  }

  &.expanded {
    width: 250px; // Sidebar is expanded
  }

  .top {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 20px;

    .hamburger-icon {
      cursor: pointer;
      font-size: 24px; // Hamburger icon size
      color: #770737; // Accent color
    }

    .logo {
      font-size: 24px;
      font-weight: bold;
      color: #770737;
      text-align: center;
      transition: all 0.3s ease-in-out;

      &:hover {
        color: #4a0026; // Slightly darker on hover
      }
    }
  }

  hr {
    margin: 0;
    border: none;
    height: 1px;
    background-color: #e0e0e0;
  }

  .center {
    flex: 1;
    padding: 10px;

    .title {
      font-size: 12px;
      color: #999;
      margin-bottom: 10px;
    }

    ul {
      list-style: none;
      padding: 0;
      margin: 0;

      li {
        display: flex;
        align-items: center;
        padding: 10px;
        cursor: pointer;
        border-radius: 8px;
        transition: background-color 0.3s ease, color 0.3s ease;

        &:hover {
          background-color: #f0f0f0;
        }

        .icon {
          color: #770737;
          font-size: 18px;
        }

        span {
          margin-left: 10px;
          font-size: 16px;
          font-weight: 500;
          color: #333;
        }

        &:hover span {
          font-weight: 600;
        }
      }
    }
  }

  .bottom {
    padding: 10px;
    display: flex;
    justify-content: space-between;

    .colorOption {
      width: 20px;
      height: 20px;
      border-radius: 50%;
      border: 1px solid #999;
      cursor: pointer;

      &:first-child {
        background-color: white;
      }

      &:last-child {
        background-color: black;
      }
    }
  }
}