.navbar {
  width: 100%;
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 20px;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);

  .wrapper {
    display: flex;
    justify-content: space-between;
    width: 100%;
    align-items: center;

  .title {
    font-family: 'Helvetica Neue', Arial, sans-serif;
    font-size: 24px;
    font-weight: bold;
    color: #770737; 
    letter-spacing: 1px; 
    position: absolute;  
    left: 50%;         
    transform: translateX(-50%);  
    }

  }
}