    .login {
      position: relative;
      width: 100vw;
      height: 100vh;
      overflow: hidden;
    
      .video-bg {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
        z-index: -1;
      }
    
      .login-content {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 300px;
        background-color: rgba(255, 255, 255, 0.9);
        padding: 40px;
        border-radius: 10px;
        text-align: center;
    
        .logo {
          width: 250px;
          height:auto;
          margin-bottom: 40px;
        }
    
        form {
          display: flex;
          flex-direction: column;
    
          input {
            margin-bottom: 15px;
            padding: 10px;
            font-size: 16px;
            border: 1px solid #ccc;
            border-radius: 5px;
            outline: none;
            transition: border-color 0.3s ease;
    
            &:focus {
              border-color: 	#953553;
            }
          }
    
          button {
            padding: 10px;
            font-size: 16px;
            font-weight: bold;
            color: #fff;
            background-color: 	#C21E56; // Use the purple color as shown in the image
            border: none;
            border-radius: 5px;
            cursor: pointer;
            transition: background-color 0.3s ease;
    
            &:hover {
              background-color: #550055; // Darker shade of purple for hover effect
            }
          }
    
          span {
            color: red;
            margin-top: 10px;
          }
        }
      }
    }