.chart-container {
  width: 100%;
  max-width: 600px;
  margin: 0 auto;
  padding: 10px;
  box-sizing: border-box;
}

.chart-title {
  text-align: center;
  font-size: 1.5rem;
  font-weight: bold;
  color: #770737;
  margin-bottom: 20px;
}

@media (max-width: 768px) {
  .chart-container {
    max-width: 100%;
    padding: 5px;
  }

  .chart-title {
    font-size: 1.3rem;
  }

  .charts {
    width: 100%;
  }

  .pieChart {
    width: 100%;
  }
}
